<template>
  <div class="flex h-full">
    <div class="my-auto w-full mx-12 lg:mx-20 xl:mx-60 lg:py-20">
      <div class="my-2">
        <h2 class="text-3xl font-semibold my-4">
          Choose the Items you want to buy
        </h2>
        <p class="text-black my-4">
          Select the items you are willing to buy. Don't worry, you can always waste time adding items later.
        </p>
        <div
          class="bg-teal-01 text-teal-04 inline-flex items-center px-4 py-2 rounded-md"
        >
          <i class="material-icons mr-2">info</i>
          <p>
            Click
            <span class="font-semibold">"Go to Basic Settings"</span> to gain
            access to our full catalog.
          </p>
        </div>
      </div>
      <div class="mb-10">
        <ProductList
          :products="statePriceSyncDevices"
          :collapsible="statePriceSyncDevices.length > 0"
          mark-selected-all
          hide-show-more
          @select-product="handleRemovePriceSyncDevice"
        >
          <template #separator-label>
            <span class="ml-2"
              >Selected ({{ totalSelectedPriceSyncDevices }})</span
            >
          </template>
        </ProductList>
      </div>
      <div class="my-10">
        <ProductList
          :products="filteredSyncableDevices"
          :loading-show-more="loadingSyncableDevices"
          :hide-show-more="isLastSyncableDevices"
          :collapsible="statePriceSyncDevices.length > 0"
          @show-more="handleShowMoreDevices"
          @select-product="handleDevicePriceSync"
        >
          <template #separator-label>
            <span class="ml-2"
              >Unselected ({{ totalAvailableSyncableDevices }})</span
            >
          </template>
        </ProductList>
      </div>
      <div class="mb-20">
        <Button
          @click="handleEjectPriceSync"
          :disabled="false"
          :loading="loadingPriceSyncDevices.eject_price_sync"
          class="mx-2"
          color="teal"
          view="outlined"
          >Go to Basic Settings</Button
        >
        <Tooltip
          :disabled="!isIncompletePriceSyncs"
          placement="top"
          content="Please select one option to continue"
        >
          <template #default>
            <div class="inline-block w-max">
              <Button
                class="mx-2"
                color="teal"
                :loading="loadingPriceSyncDevices.price_sync"
                :disabled="isIncompletePriceSyncs"
                @click="handlePriceSyncs"
                >Continue <i class="material-icons ml-2">east</i></Button
              >
            </div>
          </template>
        </Tooltip>
      </div>
    </div>
    <Modal
      v-model="modalPriceSyncDevices"
      @close="handleUnsetPriceSyncDevice"
      size="lg"
      hide-header
    >
      <template #default>
        <div class="border-b border-grey-01 px-10 pb-5 mb-5 -mx-12">
          <h6 class="font-semibold">Price Sync</h6>
          <!-- <div class="flex items-center">
            <a href="https://help.reusely.com/en/articles/9176424-how-price-sync-works" target="_blank" noreferrer noopener whitelabel="remove" class="material-icons mx-2">help</a>
          </div> -->
        </div>
        <div class="space-y-10">
          <div
            v-loading="loadingPriceSyncDevices.active_device || loadingPricingSourceCompanies"
            class="space-y-8">
            <div class="flex items-center gap-x-4">
              <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">1</div>
              <span class="font-semibold text-sm">Select your data source</span>
            </div>
            <div class="grid lg:grid-cols-4 md:grid-cols-2 grid-cols-1 gap-4">
              <div
                v-for="(source, n) in statePricingSourceCompanies"
                :key="n"
                :class="[
                  'border',
                  source.sub_domain
                    ? 'hover:border-teal-04 cursor-pointer'
                    : '',
                  source.sub_domain === dataPriceSyncDevices.source
                    ? 'border-teal-04'
                    : 'border-grey-01',
                  statePricingSourceCompanies.length === 1 && source.sub_domain
                    ? 'opacity-50 cursor-not-allowed'
                    : 'opacity-100',
                  'rounded-lg p-4'
                ]"
                @click="handleCheckActiveDevice(source.sub_domain)">
                <template v-if="source.sub_domain">
                  <div class="flex flex-col items-center justify-center gap-6 h-full">
                    <img :src="source.logo" class="pricing-source-logo" />
                    <div class="flex justify-center items-center gap-x-2 max-w-max">
                      <i v-if="dataPriceSyncDevices.source === source.sub_domain" class="material-icons text-teal-04">check_circle</i>
                      <span class="text-sm font-semibold inline-block w-full text-center break-words">{{ source.name }}</span>
                    </div>
                    <div v-if="stateCurrentCurrency && stateCurrentCountryCode" class="bg-grey-01 bg-opacity-50 p-2 px-4 rounded-md flex items-center justify-start gap-x-4">
                      <template v-if="stateCurrentCurrency !== source.currency">
                        <div class="flex items-center gap-x-2">
                          <img v-if="source.country_code" :src="`/flag/${source.country_code.toLowerCase()}.svg`" width="28" height="24" />
                          <span class="text-sm">{{ source.currency }}</span>
                        </div>
                        <span class="bg-teal-04 rounded-full w-6 h-6 flex justify-center items-center text-white text-lg">
                          <i class="material-icons">arrow_right_alt</i>
                        </span>
                      </template>
                      <div class="flex items-center gap-x-2">
                        <img :src="`/flag/${stateCurrentCountryCode.toLowerCase()}.svg`" width="28" height="24" />
                        <span class="text-sm">{{ stateCurrentCurrency }}</span>
                      </div>
                    </div>
                    <div v-if="source.is_support_sell_instantly">
                      <Tooltip placement="top">
                        <button class="text-orange-04 bg-orange-01 px-2 py-1 rounded-full flex justify-center items-center">
                          <svg width="17" height="16" viewBox="0 0 17 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M6.25 3V8.5H7.75V13L11.25 7H9.25L11.25 3H6.25Z" fill="currentColor"/>
                          </svg>
                          <span class="text-xs">Supports Sell Instantly</span>
                        </button>
                        <template #content>
                          <div class="w-60 text-xs">
                            You can sell excess inventory directly from the inventory page to this pricing source.
                          </div>
                        </template>
                      </Tooltip>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <div class="flex flex-col items-center justify-center gap-6 h-full">
                    <img src="/icon/settings/pricing-source-custom.svg" alt="custom" class="w-48 mx-auto" />
                    <span class="text-sm font-semibold">Connect Any Data</span>
                    <button class="bg-teal-04 text-white px-4 py-2 rounded-lg hover:text-white text-sm" @click="handleIntercomeMessage">Learn More</button>
                  </div>
                </template>
              </div>
            </div>
          </div>
          <template v-if="stateCompanySource.status !== null">
            <div v-if="stateCompanySource.status === 'available'" class="space-y-2">
              <div class="flex items-center gap-x-4 mb-5">
                <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">2</div>
                <span class="font-semibold text-sm">Set your pricing rules</span>
              </div>
              <div class="max-w-lg">
                <RadioGroup
                  v-model="dataPriceSyncDevices.type"
                  class="radio--full"
                  @change="handleChangePriceSyncRequirementType"
                >
                  <RadioButton label="3">Match</RadioButton>
                  <RadioButton label="2">Markup</RadioButton>
                  <RadioButton label="1">Markdown</RadioButton>
                </RadioGroup>
              </div>
              <div v-if="dataPriceSyncDevices.type != 3" class="grid grid-cols-2 max-w-lg gap-2">
                <RadioGroup
                  v-model="dataPriceSyncDevices.amount_type"
                  @change="handleChangePriceSyncAmountType"
                  class="radio--full">
                  <RadioButton label="1">% Percentage</RadioButton>
                  <RadioButton label="0">{{ stateCurrentCurrencySymbol }} Fixed</RadioButton>
                </RadioGroup>
                <Input
                  v-model="dataPriceSyncDevices.amount_price"
                  type="number"
                  class="w-full"
                  icon
                  icon-block
                  :currency="true"
                  :right="
                    dataPriceSyncDevices.amount_type == '0' ? false : true
                  "
                  :min="0"
                >
                  <template #icon>
                    <span
                      class="text-grey-03 text-lg"
                      v-if="dataPriceSyncDevices.amount_type == '0'"
                      >{{ stateCurrentCurrencySymbol }}</span
                    >
                    <span class="text-grey-03 text-lg" v-else>%</span>
                  </template>
                </Input>
              </div>  
            </div>
            <div v-else class="bg-orange-lighten text-orange-04 px-4 py-4 rounded">
              Unfortunately, the pricing source does not have pricing for the
              device(s) you are requesting.
            </div>
          </template>
          <div v-if="dataPriceSyncDevices.is_computer && !loadingPriceSyncDevices.active_device && dataPriceSyncDevices.source && !loadingPricingSourceCompanies" class="space-y-2">
            <div class="flex items-center gap-x-4 mb-5">
              <div class="bg-teal-04 w-6 h-6 rounded-full flex items-center justify-center text-white">3</div>
              <span class="font-semibold text-sm">Preview Price from Model</span>
            </div>
            <div class="max-w-lg">
              <Select
                :modelValue="stateSelectedDevicePriceSyncPreview"
                :items="stateAvailableDevicesPriceSyncPreview"
                name="name"
                by="id"
                placeholder="Item to preview"
                block
                :loading="
                  loadingPriceSyncDevices.active_device ||
                  loadingPriceSyncDevices.available_devices_sync_preview ||
                  loadingPriceSyncDevices.device_sync_preview ||
                  loadingPricingSourceCompanies
                "
                @change="handleSelectDevicePriceSyncPreview"
                max-height="200px"
              />
            </div>
            <Transition name="slide-fade">
              <TablePreviewPricingMac
                v-loading="loadingPriceSyncDevices.device_sync_preview"
                v-if="
                  stateCompanySource.status === 'available' &&
                  isDevicePriceSyncPreview
                "
                :item="stateDevicePriceSyncPreview"
                :company-source="selectedCompanySource"
                :currency-symbol="stateCurrentCurrencySymbol"
              />
            </Transition>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="flex justify-between items-center">
          <a
            class="text-red-04 hover:text-red-03 px-6 py-2"
            href="#!"
            @click.prevent="modalPriceSyncDevices = false"
            >Cancel</a
          >
          <Button
            color="teal"
            :disabled="isIncompletePriceSyncDevice"
            @click="handleAddPriceSyncDevice"
            >Save</Button
          >
        </div>
      </template>
    </Modal>
    <!-- <OnboardingReferralCode
      @skip="handleEjectPriceSync"
      @apply="handleEjectPriceSync"
    /> -->
  </div>
</template>
<script>
import { defineComponent, computed, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useOnboarding } from 'store/pinia/onboarding'

// Composables
import {
  useSyncableDevices,
  usePricingSourceCompanies,
  useProductPriceSync
} from 'composable/onboarding'
import { useUser } from 'composable/user'
import { useFeatureFlag, isWhiteLabel } from 'composable/whitelabel'
import { isObject } from 'utils/is'

// Components
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'components/Select.vue'
import ProductList from 'components/Onboarding/ProductList.vue'
import TablePreviewPricingMac from 'components/Onboarding/SyncPricing/TablePreviewPricingMac.vue'
import OnboardingReferralCode from 'components/Features/Onboarding/OnboardingReferralCode.vue'

// Utils
import debounce from 'utils/debounce'

export default defineComponent({
  components: {
    Button,
    Modal,
    Input,
    Select,
    ProductList,
    TablePreviewPricingMac,
    OnboardingReferralCode
  },
  async setup() {
    const storePiniaOnboarding = useOnboarding()
    const { me } = useUser()
    const {
      state: stateSyncableDevices,
      filters: filtersSyncableDevices,
      loading: loadingSyncableDevices,
      actionGetDevices
    } = useSyncableDevices()
    const {
      state: statePricingSourceCompanies,
      data: dataPricingSourceCompanies,
      loading: loadingPricingSourceCompanies,
      actionGetCompanies,
      unsetPricingSourceCompanies
    } = usePricingSourceCompanies()
    const {
      state: statePriceSyncDevices,
      stateCompanySource,
      stateCurrentCurrency,
      stateCurrentCountryCode,
      stateCurrentCurrencySymbol,
      stateAvailableDevicesPriceSyncPreview,
      stateSelectedDevicePriceSyncPreview,
      stateDevicePriceSyncPreview,
      data: dataPriceSyncDevices,
      loading: loadingPriceSyncDevices,
      modal: modalPriceSyncDevices,
      isIncompletePriceSyncDevice,
      isIncompletePriceSyncs,
      priceSyncDevice,
      removePriceSyncDevice,
      unsetPriceSyncDevice,
      selectDevicePriceSyncPreview,
      actionPriceSyncs,
      actionSelectingProduct,
      actionCheckActiveDevice,
      actionEjectPriceSync,
      actionAvailableDevicesPreview,
      actionDevicePriceSyncPreview
    } = useProductPriceSync()
    const { features } = useFeatureFlag()
    const route = useRoute()
    const router = useRouter()

    const selectedPriceSyncDevicesId = computed(() =>
      statePriceSyncDevices.value.map((device) => device.category_id)
    )
    const filteredSyncableDevices = computed(() =>
      stateSyncableDevices.value.data.filter(
        (device) =>
          !selectedPriceSyncDevicesId.value.includes(device.category_id)
      )
    )
    const totalSelectedPriceSyncDevices = computed(
      () => statePriceSyncDevices.value.length
    )
    const totalAvailableSyncableDevices = computed(() => {
      const total =
        stateSyncableDevices.value.total - totalSelectedPriceSyncDevices.value
      if (isNaN(total)) return ''
      return total
    })
    const isDevicePriceSyncPreview = computed(
      () =>
        stateSelectedDevicePriceSyncPreview.value.id &&
        dataPriceSyncDevices.is_computer
    )
    const isSelectedDevicePriceSyncPreview = computed(
      () => stateSelectedDevicePriceSyncPreview.value.id !== null
    )
    const selectedCompanySource = computed(() => {
      const companySource = statePricingSourceCompanies.value.find(
        (company) => company.sub_domain === dataPriceSyncDevices.source
      )
      if (!companySource) return null
      return {
        ...companySource,
        ...stateCompanySource.value
      }
    })
    const isLastSyncableDevices = computed(
      () =>
        stateSyncableDevices.value.current_page >=
        stateSyncableDevices.value.last_page
    )
    const intercomUserCustomAttributes = computed(() => isObject(me.value.intercom_data) ? me.value.intercom_data : {})

    const handleShowMoreDevices = async () => {
      filtersSyncableDevices.page++
      await actionGetDevices()
    }
    const handleDevicePriceSync = async (product) => {
      Object.assign(dataPricingSourceCompanies, {
        category_id: product.category_id,
        is_mac: product.is_mac
      })
      Object.assign(dataPriceSyncDevices, {
        category_id: product.category_id,
        is_computer: product.is_mac,
        name: product.name,
        image: product.image
      })
      handleAddPriceSyncDevice()
      // const isMacDevice = product.is_mac
      // if (isMacDevice) {
      //   actionAvailableDevicesPreview()
      // }

      // modalPriceSyncDevices.value = true
      // await actionGetCompanies()

      // auto select pricing source company when there's only one pricing source
      // if (statePricingSourceCompanies.value.length === 1) {
      //   dataPriceSyncDevices.source =
      //     statePricingSourceCompanies.value[0]?.sub_domain
      //   await actionCheckActiveDevice()
      // }
    }
    const handleCheckActiveDevice = async (source = null) => {
      if (!source) return
      dataPriceSyncDevices.source = source
      await actionCheckActiveDevice()
    }
    const handleAddPriceSyncDevice = () => {
      priceSyncDevice()
      modalPriceSyncDevices.value = false
    }
    const handleRemovePriceSyncDevice = (product) =>
      removePriceSyncDevice(product)
    const handleChangePriceSyncRequirementType = async (newAmountType) => {
      if (newAmountType == 3) dataPriceSyncDevices.amount_price = 0
      if (isSelectedDevicePriceSyncPreview.value)
        await actionDevicePriceSyncPreview()
    }
    const handleChangePriceSyncAmountType = async () => {
      if (isSelectedDevicePriceSyncPreview.value)
        await actionDevicePriceSyncPreview()
    }
    const handlePriceSyncs = async () =>
      await actionPriceSyncs(() => {
        router.push({
          name: route.name.includes('newCompany')
            ? 'newCompanyPayoutMethods'
            : 'onboardingPayoutMethods'
        })
      })
    const handleEjectPriceSync = async () =>
      await actionEjectPriceSync(() => {
        window.location.href = '/setting/product-catalog'
      })
    const handleUnsetPriceSyncDevice = () => {
      unsetPriceSyncDevice()
      unsetPricingSourceCompanies()
    }
    const handleSelectDevicePriceSyncPreview = async (device) => {
      selectDevicePriceSyncPreview(device)
      await actionDevicePriceSyncPreview()
    }
    const handleIntercomeMessage = () => {
      if (!isWhiteLabel.value) return window.open(
        'https://forms.clickup.com/8427959/f/816dq-50256/3ZVVXIRACGV7NVG7QD',
        '_blank'
      )

      window.CustomerSupport.use('showNewMessage', 'Hi, I would like to learn more about your custom data services')
    }
    const handleReferralCode = () => {
      storePiniaOnboarding.referralCode.modal = true
    }
    watch(
      () => dataPriceSyncDevices.amount_price,
      debounce(async () => {
        if (isSelectedDevicePriceSyncPreview.value)
          actionDevicePriceSyncPreview()
      }, 500)
    )
    await Promise.all([actionGetDevices()])
    if (import.meta.env.VITE_ENVIRONMENT === 'production' && ['https://app.reusely.com', 'https://app.refreshedtech.com'].includes(import.meta.env.VITE_API_URL)) {
      window.CustomerSupport.use('boot', {
        app_id: window.APP_ID,
        name: `${me.value.first_name} ${me.value.last_name}`, // Full name
        email: me.value.email ? me.value.email : null, // Email address
        created_at: me.value.created_at ? me.value.created_at : null, // Signup date as a Unix timestamp
        alignment: 'right',
        ...intercomUserCustomAttributes.value
      })
    }

    return {
      statePriceSyncDevices,
      filteredSyncableDevices,
      statePricingSourceCompanies,
      stateCompanySource,
      stateCurrentCurrency,
      stateCurrentCountryCode,
      stateCurrentCurrencySymbol,
      stateAvailableDevicesPriceSyncPreview,
      stateSelectedDevicePriceSyncPreview,
      stateDevicePriceSyncPreview,
      dataPriceSyncDevices,
      loadingSyncableDevices,
      loadingPriceSyncDevices,
      loadingPricingSourceCompanies,
      modalPriceSyncDevices,
      isIncompletePriceSyncDevice,
      isIncompletePriceSyncs,
      isDevicePriceSyncPreview,
      isLastSyncableDevices,
      selectedCompanySource,
      totalAvailableSyncableDevices,
      totalSelectedPriceSyncDevices,
      features,
      handleIntercomeMessage,
      handleShowMoreDevices,
      handleDevicePriceSync,
      handleCheckActiveDevice,
      handleAddPriceSyncDevice,
      handleRemovePriceSyncDevice,
      handlePriceSyncs,
      handleChangePriceSyncRequirementType,
      handleChangePriceSyncAmountType,
      handleEjectPriceSync,
      handleUnsetPriceSyncDevice,
      handleSelectDevicePriceSyncPreview,
      handleReferralCode
    }
  }
})
</script>
<style lang="scss" scoped>
.price-sync-layout {
  @apply w-full;
  &--half {
    @apply w-1/2;
  }
}
.pricing-source-logo {
  max-width: 12.5rem;
  width: 100%;
}
</style>
