<template>
  <div class="product-catalog">
    <div
      v-if="collapsible"
      @click="isCollapse = !isCollapse"
      class="product-catalog__header"
    >
      <div
        class="product-catalog__separator-icon"
        :style="{ transform: isCollapse ? 'rotate(180deg)' : 'rotate(0deg)' }"
      >
        <i class="material-icons">expand_more</i>
      </div>
      <div class="product-catalog__separator-counter">
        <slot name="separator-label"></slot>
      </div>
      <div class="product-catalog__separator-line">
        <hr class="w-full text-grey-02" />
      </div>
    </div>
    <transition name="slide-fade">
      <div v-show="isCollapse" class="product-catalog__list">
        <div class="product-list">
          <div
            v-for="(product, n) in products"
            :key="n"
            :class="[
              'product-list__card',
              markSelectedAll ? 'product-list__card--selected' : ''
            ]"
            @click="handleSelectProduct(product)"
          >
            <div v-if="markSelectedAll" class="product-list__checklist">
              <i class="material-icons">done</i>
            </div>
            <div
              class="product-list__product-icon"
              :style="`-webkit-mask-image: url('${product.image}')`"
            ></div>
            <div class="product-list__product-title">
              <slot name="title">
                {{ product.name }}
              </slot>
            </div>
          </div>
          <div
            v-if="!hideShowMore"
            @click="!loadingShowMore && handleShowMore()"
            :class="[
              'product-list__card-more',
              loadingShowMore ? 'product-list__card-more--loading' : ''
            ]"
          >
            <div class="product-list__more-icon select-none">
              <template v-if="loadingShowMore">
                <i class="material-icons icon-xl animate-spin">refresh</i>
              </template>
              <template v-else>
                <i class="material-icons icon-xl">arrow_circle_right</i>
              </template>
            </div>
            <div class="product-list__more-label">
              <p>{{ loadingShowMore ? 'Loading...' : 'Show More' }}</p>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import { defineComponent, ref } from 'vue'
export default defineComponent({
  props: {
    products: {
      type: Array,
      default: () => []
    },
    loadingShowMore: {
      type: Boolean,
      default: false
    },
    hideShowMore: {
      type: Boolean,
      default: false
    },
    collapsible: {
      type: Boolean,
      default: false
    },
    markSelectedAll: {
      type: Boolean,
      default: false
    }
  },
  emits: ['show-more', 'select-product'],
  setup(props, { emit }) {
    const isCollapse = ref(true)
    const handleShowMore = () => emit('show-more', true)
    const handleSelectProduct = (selectedProduct) =>
      emit('select-product', selectedProduct)
    return {
      isCollapse,
      handleShowMore,
      handleSelectProduct
    }
  }
})
</script>
