  &__card,
  &__card-more {
    @apply transition duration-300 bg-white px-4 py-8 border border-grey-01 text-center rounded-lg my-2 relative;
    #{$self}__product-title {
      @apply font-semibold my-4 text-lg;
    }
    #{$self}__product-icon {
      @apply transition duration-300 h-16 w-16 bg-grey-02 mx-auto mt-4 mb-8;
      -webkit-mask-size: contain;
      -webkit-mask-position: center;
      -webkit-mask-repeat: no-repeat;
    }
    #{$self}__checklist {
      @apply absolute right-4 top-4 flex items-center justify-center w-6 h-6 bg-teal-04 rounded-full text-white;
    }
    #{$self}__more-label {
      @apply font-semibold text-lg my-4;
    }
    #{$self}__more-icon {
      i {
        @apply transition duration-300 text-grey-02;
      }
    }
    &--selected {
      @apply border-teal-04;
      #{$self}__product-title {
        @apply text-teal-04;
      }
      #{$self}__product-icon {
        @apply bg-teal-04;
      }
    }
    &:hover {
      @apply border-teal-04 cursor-pointer;
      #{$self}__product-title {
        @apply text-teal-04;
      }
      #{$self}__product-icon {
        @apply bg-teal-04;
      }
      #{$self}__more-icon > i,
      #{$self}__more-label {
        @apply text-teal-04;
      }
    }
  }
  &__card-more {
    @apply flex items-center justify-center flex-col;
    &--loading {
      &:hover {
        @apply border-grey-lighten cursor-default;
        #{$self}__product-title {
          @apply text-grey-04;
        }
        #{$self}__product-icon {
          @apply bg-grey-04;
        }
        #{$self}__more-icon > i,
        #{$self}__more-label {
          @apply text-grey-04;
        }
      }
      @apply bg-grey-lighten border-grey-lighten text-grey-04;
    }
  }
}
</style>
