<template>
  <div>
    <div v-if="companySource !== null" class="flex items-center mb-5">
      <p class="text-dark font-semibold mr-8">
        Source of Pricing:
        <span class="font-normal ml-4">{{ companySource.name }}</span>
      </p>
      <div class="flex items-center mr-8">
        <label class="font-semibold mr-4">Base Price: </label>
        <Input :modelValue="item.base_price" type="number" disabled icon dense>
          <template #icon>
            {{ currencySymbol || '$' }}
          </template>
        </Input>
      </div>
      <img
        v-if="companySource.logo"
        :src="companySource.logo"
        alt="company-logo"
        class="h-12 mx-4"
      />
    </div>
    <div class="mb-8">
      <p class="font-semibold mb-4 text-dark">Set Condition Pricing</p>
      <Table :data="item.condition_price">
        <TableColumn prop="name" label="Condition Name" width="150" />
        <TableColumn label="Set Percentage from Base Price" width="250">
          <template #default="scope">
            <Input
              :modelValue="scope.row.percentage"
              type="number"
              disabled
              icon
              right
              block
              dense
            >
              <template #icon>
                <span class="text-base">%</span>
              </template>
            </Input>
          </template>
        </TableColumn>
        <TableColumn prop="last_update" label="Last Update">
          <template #default="scope">
            <span>{{
              scope.row.last_update ? scope.row.last_update : '-'
            }}</span>
          </template>
        </TableColumn>
      </Table>
    </div>
    <div class="mb-8">
      <Table :data="item.specs" class="w-full">
        <TableColumn prop="name" label="Spec Category" />
        <TableColumn label="Spec name" width="150">
          <template #default="{ row }">
            {{ row.spec?.spec_name }}
          </template>
        </TableColumn>
        <TableColumn prop="name" label="Spec (%)" width="100">
          <template #default="{ row }">
            <Input
              :modelValue="row.spec?.price"
              type="number"
              disabled
              icon
              right
              block
              dense
            >
              <template #icon>
                <span class="text-base">%</span>
              </template>
            </Input>
          </template>
        </TableColumn>
        <TableColumn
          v-for="(condition, index) of item.condition_price"
          :key="index"
          min-width="170"
        >
          <template #header>
            <div class="flex justify-start items-center">
              <div>{{ condition.name }}</div>
              <div class="ml-2">
                <Popover :width="250" placement="bottom-start" trigger="hover">
                  <h6 class="font-semibold text-base text-black mb-3">
                    Condition: {{ condition.name }}
                  </h6>
                  <div v-if="condition?.terms">
                    <p
                      class="text-dark mb-2"
                      v-html="
                        JSON.parse(condition.terms)?.length > 1
                          ? sanitize(JSON.parse(condition.terms)[0])
                          : ''
                      "
                    />
                    <ul class="list w-full">
                      <template
                        v-for="(rule, n) in JSON.parse(condition.terms)"
                        :key="n"
                      >
                        <li v-if="n !== 0" class="break-normal">
                          {{ sanitize(rule) }}
                        </li>
                      </template>
                    </ul>
                  </div>
                  <template #reference>
                    <div class="text-teal-04 text-xs">
                      <span class="material-icons text-lg">info</span>
                    </div>
                  </template>
                </Popover>
              </div>
            </div>
          </template>
          <template #default="{ row }">
            <Currency :value="row.spec?.conditions?.[index]?.condition_value" />
          </template>
        </TableColumn>
      </Table>
    </div>
  </div>
</template>
<script>
import { defineComponent, computed } from 'vue'

// Utils
import { sanitize } from 'utils/dom'

// Components
import Input from 'bbUi/components/input/input.vue'
import Currency from 'components/Currency.vue'

export default defineComponent({
  components: {
    Input,
    Currency
  },
  props: {
    item: {
      type: Object,
      default: {}
    },
    companySource: {
      type: Object,
      default: {}
    },
    currencySymbol: {
      type: String,
      default: '$'
    }
  },
  setup(props) {
    const item = computed(() => {
      const specs = []
      Object.entries(props.item.specs).forEach(([name, eachSpecs]) => {
        eachSpecs.forEach((spec) => {
          specs.push({
            name,
            spec
          })
        })
      })
      return {
        ...props.item,
        specs
      }
    })
    return {
      item,
      sanitize
    }
  }
})
</script>
